.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}

img{
    width: 100%;
    height:100%;
    object-fit: cover;
}

.hero-text{
    position: absolute;
    top: 55%;
    left:50%;
    transform: translate(-50%,-50%);
    width: 100%;
}

.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background:white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero-text p{
    font-size: 1.3rem;
    color:rgb(248, 248, 247);
    padding: 0.5rem 0.2rem 1.5rem;

}

.show{
    /* text-decoration: none; */
    background: rgb(144, 52, 133);
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: rgb(236, 233, 233);
    width:200px;
}

.show:hover{
    cursor: pointer;
}

@media screen and (max-width:555px){
    .hero{
        background-size: cover;
        width:100%;
    }
    .hero-text h1{
        padding: 10px 20px;
    }

    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }

    .hero-text .show{
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;  
          
    }
}