.books-h1{
    margin-top: 130px;
    font-size: 2.5rem;
}

.books-h2{
    padding: 20px;
    color: rgba(37, 57, 155, 0.963);
    font-size: 1.5rem;
    text-align: center;
}

.grid-wrapper{
    padding: 3rem 8rem;
    display: block;
    
}

.row{
    display: flex;
    justify-content: space-between;
    gap:20px;
    margin-bottom: 50px;
}
.bookscard{
    box-shadow: 0 5px 25px 2px rgb(230, 225, 225);
    min-height: 300px;
    width: 350px;
    margin: 10px;
    padding:10px;
    text-align: center;
    border: 2px solid rgba(21, 31, 80, 0.963);
}

.bookscard img{
    height:290px;
    width: 200px;
    /* border-radius:50%; */
}
.bookscard h3{
    padding: 25px;
    color: rgba(21, 31, 80, 0.963);
}

.books-btn{
    margin: 30px;
    color: white;
    background-color: rgba(21, 31, 80, 0.963);
    border: 2px solid white;
    border-radius: 18px;
    width: 150px;
    height: 40px;
}

.books-btn:hover{
    cursor: pointer;
    background-color: rgba(57, 84, 205, 0.963);
}

@media screen and (max-width:850px){

    .books-h2{
        padding: 4px;
        color: rgba(37, 57, 155, 0.963);
        font-size: 1.2rem;
        text-align: center;
    }
    
    .grid-wrapper{
        padding: 3rem 4rem;
        flex-direction: column;
    }  
    .row{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .bookscard{
        box-shadow: 0 5px 25px 2px rgb(230, 225, 225);
        width: 250px;
        margin: 0px;
        padding: 10px;
    }

    .bookscard img{
        height:250px;
        width: 200px;
        /* border-radius:50%; */
    }
    .bookscard h3{
        padding: 15px;
    }
}