.music{
    width:100%;
}
.music-h1{
    margin-top: 130px;
    font-size: 2.5rem;
}

.music-h2{
    padding-top: 130px;
    /* padding: 20px; */
    padding-left: 100px;
    padding-right: 100px;
    color: rgba(37, 57, 155, 0.963);
    font-size: 1.5rem;
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.grid-wrapper1{
   
    padding: 3rem;
    display: block;
    padding: 10px 10px;
    
}

.row{
    /* width:80%; */
    align-items: center;
    display: flex;
    justify-content: space-between;
    gap:20px;
    margin-bottom: 50px;
    overflow-x: scroll;
}
.musiccard{
    box-shadow: 0 5px 25px 2px rgb(141, 91, 91);
    height: 330px;
    width: 412px;
    margin: 10px;
    padding:5px;
    text-align: center;
    /* border: 2px solid rgba(80, 21, 27, 0.963); */
}

.musiccard h3{
    margin: 20px;
    color: rgba(21, 31, 80, 0.963);
}

@media screen and (max-width:850px){
    .music-h2{
        padding-top: 130px;
        padding-left: 10px;
        padding-right: 10px;
    }
    .musiccard{
        box-shadow: 0 5px 25px 2px rgb(141, 91, 91);
        height: 230px;
        width: 320px;
    }
    .row{
        /* width:80%; */
        overflow-x:visible;
    }

    iframe{
        height:215px;
        width:300px;
    }

}